@import url(https://fonts.googleapis.com/css2?family=Cinzel+Decorative&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cinzel+Decorative&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cinzel+Decorative&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cinzel+Decorative&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cinzel+Decorative&display=swap);
.About {
  text-align: center;
  background-color: #d5b970;
  color: rgb(255, 255, 255);
  font-family: 'Cinzel Decorative';
  text-align: center;
}

.About-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: top;
  font-size: calc(10px + 2vmin);
}

.About-body {
  display: grid;
  grid-template-columns: repeat(3, 3fr);
  margin-left: 50px;
  margin-right: 50px;
}

.About-border {
  border-style: solid;
  border-color: rgb(255, 255, 255);
  border-width: thin;
  margin-left: 50px;
  margin-right: 50px;
  margin-bottom: 50px;
}

.About-paragraph {
  font-family: 'EB Garamond';
  text-justify: inter-word;
  line-height: 1.6;
  margin: 10px;
}

.Contact {
  background-color: #d5b970;
  text-align: center;
  font-family: 'Cinzel Decorative';
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #ffffff;
}

.Contact-header {
  text-align: center;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.Contact-body {
  font-family: 'EB Garamond';
  font-weight: 100;
  font-size: calc(15px + 2vmin);
  text-align: center;
  letter-spacing: 10;
  margin-bottom: 50px;
}

.Social-icon {
  margin: 20px;
}

.Home {
  text-align: center;
  font-family: 'Cinzel Decorative';
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #ffffff;
}

.Home-header {
  text-align: center;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #ffffff;
  margin-left: 200px;
  margin-right: 200px;
  word-spacing: 100vh;
}

.Home-body {
  font-family: 'Cinzel Decorative';
  font-weight: 100;
  font-size: calc(25px + 2vmin);
  text-align: left;
  letter-spacing: 10;
  margin-left: 100px;
  margin-right: 800px;
}

h1 {
  font-weight: 100;
  font-size: calc(50px + 2vmin);
  text-align: center;
  letter-spacing: 10;
}

h2 {
  font-weight: lighter;
  text-align: center;
}

.Jobs {
  text-align: center;
  background-color: #ffffff;
  color: #d5b970;
  font-family: 'Cinzel Decorative';
  text-align: center;
  margin-bottom: 50px;
}

.Jobs-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: top;
  font-size: calc(10px + 2vmin);
}

.Jobs-body {
  font-family: 'EB Garamond';
  display: inline-grid;
  grid-template-columns: repeat(2, 2fr);
  /* margin-left: 250px;
  margin-right: 250px; */
}

.Jobs-paragraph {
  font-family: 'EB Garamond';
  text-justify: inter-word;
  line-height: 1.6;
  margin: 10px;
  margin-left: 60px;
  margin-right: 60px;
  font-size: calc(8px + 2vmin);
}

.Jobs-image {
  height: 40vmin;
  pointer-events: none;
}

.PageNotFound {
  text-align: center;
}

.PageNotFound-header {
  background-color: #d5b970;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: rgb(0, 0, 0);
}

.PageNotFound-logo {
  height: 40vmin;
  pointer-events: none;
}

.Services {
  text-align: center;
  background-color: #ffffff;
  color: #d5b970;
  font-family: 'Cinzel Decorative';
  text-align: center;
  margin-bottom: 50px;
}

.Services-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: top;
  font-size: calc(10px + 2vmin);
}

.Services-body {
  font-family: 'EB Garamond';
  display: inline-grid;
  grid-template-columns: repeat(3, 3fr);
  /* margin-left: 250px;
  margin-right: 250px; */
}

.Services-bordered {
  border-left: solid #d5b970;
  border-right: solid #d5b970;
}

.Services-paragraph {
  font-family: 'EB Garamond';
  text-justify: inter-word;
  line-height: 1.6;
  margin: 10px;
  margin-left: 50px;
  margin-right: 50px;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu',
    'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

